<template>
    <div class="echart">
        <el-card shadow="hover">
            <el-col>
                <el-card shadow="never">
                    <el-form :inline="true">
                        <el-form-item label="qq号">
                            <el-input v-model="formInline.user" placeholder="数据1"></el-input>
                        </el-form-item>
                        <el-form-item label="data2">
                            <el-input v-model="formInline.region" placeholder="数据2"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="onSubmit">查询</el-button>
                        </el-form-item>
                    </el-form>
                </el-card>
            </el-col>
            <el-col :span="20">
                <div id="echart" style="width: 100%; height: 600px"></div>
            </el-col>
        </el-card>
    </div>
</template>
<script>
export default {
    name: 'echart',
    components: {},
    data() {
        return {
            formInline: {
                user: '',
                region: ''
            },
            option: {
                title: {
                    text: "测试用例",
                    left: "center",
                },
                tooltip: {
                    trigger: "item",
                },
                legend: {
                    orient: "vertical",
                    left: "left",
                },
                series: [
                    {
                        name: "数量",
                        type: "pie",
                        radius: "50%",
                        data: [
                            { value: 1048, name: "测试用例1" },
                            { value: 735, name: "测试用例2" },
                            { value: 580, name: "测试用例3" },
                        ],
                        emphasis: {
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: "rgba(0, 0, 0, 0.5)",
                            },
                        },
                    },
                ],
            },
        };
    },
    created() {
    },
    methods: {
        onSubmit() {
            console.log("submit")
        },
        drawChart() {
            const myChart = this.$echarts.init(document.getElementById("echart"));
            myChart.setOption(this.option);
        },
    },
    mounted() {
        this.drawChart();
    },
};
</script>
<style>
#echart {
    margin: 30px;
}
</style>