-->
<template>
    <div class="Tab">
        <el-col :span="4">
            <el-menu default-active="2" class="el-menu-vertical-demo daohang" @open="handleOpen" @close="handleClose"
                background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
                <h5>
                    <el-avatar @click="drawer = true" :size="50">{{
                    this.$attrs.id
                    }}</el-avatar>
                    <h2 @click="drawer = true">{{this.$attrs.id}}</h2>
                </h5>
                <el-menu-item index="1" @click="getTo(1)">
                    <i class="el-icon-location"></i>
                    <span slot="title">消息转发</span>
                </el-menu-item>
                <el-menu-item index="2" @click="getTo(2)">
                    <i class="el-icon-menu"></i>
                    <span slot="title">图状展示</span>
                </el-menu-item>
                <el-menu-item index="3">
                    <i class="el-icon-document"></i>
                    <span slot="title">导航三</span>
                </el-menu-item>
                <el-menu-item index="4">
                    <i class="el-icon-setting"></i>
                    <span slot="title">导航四</span>
                </el-menu-item>
            </el-menu>
        </el-col>
        <!-- 弹出框 -->
        <el-drawer direction='ltr' title="我是标题" :visible.sync="drawer" :with-header="false">
            <h1>欢迎{{this.$attrs.id}}管理员登录使用！</h1>
            <el-form>
                <el-form-item>
                    <el-select v-model="form.region" placeholder="  请选择qq机器人">
                        <el-option label="1123123" value="1123123"></el-option>
                        <el-option label="123213" value="123213"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="upaccount">提交账号</el-button>
                </el-form-item>
            </el-form>
            <el-button type="danger" round @click="outlogin">
                登出账号
            </el-button>
        </el-drawer>
    </div>
</template>
<script>
export default {
    data() {
        return {
            drawer: false,
            form: {
                region: ''
            }
        };
    },
    methods: {
        getTo(index) {
            if(index==1){
                this.$router.push({
                name: 'forward',
                params: { userId: this.$attrs.id }
            })
            }else if(index==2){
                this.$router.push({
                name: 'echarts',
                params: { userId: this.$attrs.id }
            })
            }
        },
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        upaccount() {
            // axios提交账号和获取数据渲染双向绑定
            this.$message({
                type: 'success',
                message: '已获取数据!'
            });
        },
        outlogin() {
            this.$confirm('此操作退出账号, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // 加入退出登录的操作
                this.$message({
                    type: 'success',
                    message: '已登出!'
                });
                this.$router.push({
                name: 'home',
                params: { userId: this.$attrs.id }
            })  
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消操作!'
                });
            });
        },
    },
};
</script>
<style>
.daohang {
    padding-bottom: 400px;
}
</style>